import s from './App.module.css';
import React, {useEffect, useState} from "react";
import Navbar from "./Components/Navbar/Navbar";
import MainPageContainer from "./Components/MainPage/MainPageContainer";
import {connect} from "react-redux";
import {PageLoader} from "./Components/Common/Loaders/PageLoader/PageLoader";
import {setPhotos} from "./Redux/main-reducer";
import i18next from "i18next";
import {Redirect, Route} from "react-router-dom";

function App(props) {

    const keyWordsEn = [
        'dogs', 'cats', 'coffee', 'summer', 'winter', 'autumn', 'spring', 'home', 'baby', 'cars', 'animals',
        'people', 'woman', 'man', 'clothes', 'wedding', 'food', 'new year', 'christmas', 'easter', 'dress',
        'forest', 'sea', 'ocean', 'beach', 'sun', 'space', 'moon', 'sky', 'night', 'grass', 'morning',
        'love', 'glass', 'water', 'fire', 'air', 'earth', 'plants', 'planet', 'color', 'make up', 'light'
    ];
    const keyWordsRus = [
        'ÑÐ¾Ð±Ð°ÐºÐ¸', 'ÐºÐ¾Ñ‚Ñ‹', 'ÐºÐ¾Ñ„Ðµ', 'Ð»ÐµÑ‚Ð¾', 'Ð·Ð¸Ð¼Ð°', 'Ð¾ÑÐµÐ½ÑŒ', 'Ð²ÐµÑÐ½Ð°', 'Ð´Ð¾Ð¼', 'Ñ€ÐµÐ±ÐµÐ½Ð¾Ðº', 'Ð¼Ð°ÑˆÐ¸Ð½Ñ‹', 'Ð¶Ð¸Ð²Ð¾Ñ‚Ð½Ñ‹Ðµ',
        'Ð»ÑŽÐ´Ð¸', 'Ð¶ÐµÐ½Ñ‰Ð¸Ð½Ð°', 'Ð¼ÑƒÐ¶Ñ‡Ð¸Ð½Ð°', 'Ð¾Ð´ÐµÐ¶Ð´Ð°', 'ÑÐ²Ð°Ð´ÑŒÐ±Ð°', 'ÐµÐ´Ð°', 'Ð½Ð¾Ð²Ñ‹Ð¹ Ð³Ð¾Ð´', 'Ñ€Ð¾Ð¶Ð´ÐµÑÑ‚Ð²Ð¾', 'Ð¿Ð°ÑÑ…Ð°', 'Ð¿Ð»Ð°Ñ‚ÑŒÐµ',
        'Ð»ÐµÑ', 'Ð¼Ð¾Ñ€Ðµ', 'Ð¾ÐºÐµÐ°Ð½', 'Ð¿Ð»ÑÐ¶', 'ÑÐ¾Ð»Ð½Ñ†Ðµ', 'ÐºÐ¾ÑÐ¼Ð¾Ñ', 'Ð»ÑƒÐ½Ð°', 'Ð½ÐµÐ±Ð¾', 'Ð½Ð¾Ñ‡ÑŒ', 'Ñ‚Ñ€Ð°Ð²Ð°', 'ÑƒÑ‚Ñ€Ð¾',
        'Ð»ÑŽÐ±Ð¾Ð²ÑŒ', 'ÑÑ‚ÐµÐºÐ»Ð¾', 'Ð²Ð¾Ð´Ð°', 'Ð¾Ð³Ð¾Ð½ÑŒ', 'Ð²Ð¾Ð·Ð´ÑƒÑ…', 'Ð·ÐµÐ¼Ð»Ñ', 'Ñ€Ð°ÑÑ‚ÐµÐ½Ð¸Ñ', 'Ð¿Ð»Ð°Ð½ÐµÑ‚Ð°', 'Ñ†Ð²ÐµÑ‚', 'Ð¼Ð°ÐºÐ¸ÑÐ¶', 'ÑÐ²ÐµÑ‚'
    ];

    const [language, setLanguage] = useState('ru');
    const [shortWords, setShortWords] = useState([]);
    const [page, setPage] = useState(1);
    const [body, setBody] = useState();

    let words = [];
    let str = '';
    let i = 0;
    let keysWords = [];
    const createWord = (count, lang) => {
        if (lang === 'ru') {
            keysWords = keyWordsRus;
        } else if (lang === 'en') {
            keysWords = keyWordsEn;
        }
        for (i; i < count; i++) {
            str = keysWords[Math.floor(Math.random() * keysWords.length)];
            if (words.includes(str)) {
                createWord(count, lang);
            } else {
                words.push(str);
            }
        }
        setShortWords(words);
        i = 0;
        words = [];
    }

    const changeLanguage = lang => {
        i18next.init({
            lng: lang,
            resources: require(`./languages/${lang}.json`)
        });
        setLanguage(lang);
        createWord(7, lang);
    }

    useEffect(() => {
        changeLanguage(language);
        setBody(document.querySelector('body'));
    }, [])

    return (
        <div className={props.isInitialize && s.App}>

            {/*<Redirect from="/pexels-app" to="/"/>*/}

            {
                props.isInitialize &&
                <Navbar setPhotos={props.setPhotos} changeLanguage={changeLanguage} setPage={setPage} body={body}/>
            }

            <MainPageContainer
                page={page}
                setPage={setPage}
                shortWords={shortWords}
                createWord={createWord}
                language={language}
                changeLanguage={changeLanguage}
                body={body}
            />

            {!props.isInitialize && <PageLoader/>}
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        isInitialize: state.Main.isInitialize
    }
}

const mapDispatchToProps = {setPhotos}

export default connect(mapStateToProps, mapDispatchToProps)(App);

